export default {


  name: "navigation",

  data() {
    return {

      open: false,
      checked: false

    };
  },

  methods: {
    toggle() {
      this.open = !this.open;
      this.checked = !this.checked;
    },

    toTop() {

      window.scroll(0, 0)
      fullpage_api.moveTo('page1', 2)

    }

  }
};
